import React from 'react';
import { irisSetosa, irisVersicolor } from '../source/plot-data';
import basicPerceptronModel from '../source/basic-perceptron-model';
import createPlotlyComponent from 'react-plotly.js/factory'
import FloatSlider from './../source/float-slider';

export default class extends React.Component {

  state = {
    isClient: false
  };

  plotLayout = {
    autosize: false,
    width: 500,
    height: 500,
    xaxis: {
      range: [0, 10]
    },
    yaxis: {
      range: [0, 10]
    },
    shapes: []
  };

  getClassificationLineShape() {
    const x0 = -10;
    const y0 = basicPerceptronModel.getClassificationLineY(x0);
    const x1 = +10;
    const y1 = basicPerceptronModel.getClassificationLineY(x1);
    return {
      type: 'line',
      x0, y0, x1, y1
    };
  }

  generateModelEvaluationData() {
    const points = [];
    for (let y = 0; y < 10; y++) {
      for (let x = 0; x < 10; x++) {
        points.push({ x:x, y:y, z: basicPerceptronModel.evaluate(x, y) });
      }
    }
    return {
      x: points.map(p => p.x),
      y: points.map(p => p.y),
      z: points.map(p => p.z),
      type: 'contour',
      showscale: false,
      contours: {
        coloring: 'heatmap'
      }
    }
  };

  redrawClassificationLine() {
    this.plotLayout.shapes[0] = this.getClassificationLineShape();
    this.forceUpdate();
  }

  componentDidMount() {
    this.setState({ isClient: true });
  }

  render() {
    if (!this.state.isClient) {
      return null;
    }
    const Plot = createPlotlyComponent(Plotly);
    return (
      <div>
        <Plot
          data={[irisSetosa, irisVersicolor]}
          layout={this.plotLayout}
        />
        <FloatSlider
          rangeLimit={10}
          value={basicPerceptronModel.k}
          onChange={value => { basicPerceptronModel.k = value; this.redrawClassificationLine(); }}
        />
        <div>K={basicPerceptronModel.k}</div>
        <FloatSlider
          rangeLimit={10}
          value={basicPerceptronModel.b}
          onChange={value => { basicPerceptronModel.b = value; this.redrawClassificationLine(); }}
        />
        <div>b={basicPerceptronModel.b}</div>
      </div>
    );
  }

}